import { Utils } from '../services/utils.service.js';
import Axios from "axios"

export default {
  books,
  query,
  getById,
  addReview,
  removeReview,
  addGoogleBook,
  findNextBook
}

const KEY = 'books'

const BASE_URL = 'http://localhost:3030/api/books';
var books = null;



async function query() {
  let returnedBooks = await Utils.loadFromStorage(KEY)
  if (returnedBooks) {
    console.log('Storage')
    books = returnedBooks
    Utils.saveToStorage(KEY,books)
    return books
  } else {
     returnedBooks = await Axios.get(BASE_URL)
    console.log('returnedBooks: ',returnedBooks)
    books = returnedBooks.data
    Utils.saveToStorage(KEY, books)
    return books
}

}

async function addReview(bookId, review) {
  const currBook = await getById(bookId)
  review.id = Utils.getRandomId()
  if (!currBook.reviews) {
    currBook.reviews = [review]
    Utils.saveToStorage(KEY, books)
    console.log('no prev Reviews books: ', books)

  } else {
    currBook.reviews.push(review)
    Utils.saveToStorage(KEY, books)
    console.log('books after review save are: ', books)
  }
}

async function removeReview(bookId, review) {
  const currBook = await getById(bookId)
  console.log('removeReview-bookId: ', bookId)
  console.log('removeReview-currBook: ', currBook)
  const revIdx = currBook.reviews.findIndex(rev => rev.id === review.id)
  currBook.reviews.splice(revIdx, 1)
  Utils.saveToStorage(KEY, books)
  return
}

async function getById(bookId) {
  // let currBooks = await query()
  // let currBook = await currBooks.find(book => book.id === bookId)
  // console.log('getById found book: ', currBook)
  // return Promise.resolve(currBook)
  let currBook = await books.find(book => book.id === bookId)
  return Promise.resolve(currBook)
}


async function addGoogleBook(book) {
  var convertedBook = await new ConvertedBook(book)
  books.push(convertedBook)
  Utils.saveToStorage(KEY, books)
  return convertedBook
}

function ConvertedBook(book) {
  const currBook = {}
  currBook.id = book.id
  currBook.title = book.volumeInfo.title
  currBook.subtitle = book.volumeInfo.subtitle
  currBook.authors = book.volumeInfo.authors
  currBook.publishedDate = book.volumeInfo.publishedDate
  currBook.description = book.volumeInfo.description
  currBook.pageCount = book.volumeInfo.pageCount
  currBook.categories = book.volumeInfo.categories
  currBook.listPrice = {
    isOnSale: book.saleInfo.saleability === 'FREE' ? 'free' : false,
    currencyCode: book.saleInfo.country,
    amount: book.saleInfo.listPrice ? book.saleInfo.listPrice.amount : ''
  }
  book.volumeInfo.imageLinks ? currBook.thumbnail = book.volumeInfo.imageLinks.thumbnail : ''
  currBook.language = book.volumeInfo.language
  return currBook
}

async function findNextBook(bookId) {
  const currBook = await getById(bookId)
  const idx = books.findIndex(book => book.id === currBook.id)
  const nextBook = books[idx + 1]
  return nextBook
}
