<template>
  <div>
    <addBook></addBook>
    <bookFilter></bookFilter>
    <bookList @selected="selectBook"></bookList>
  </div>
</template>
<script>
import bookFilter from "../components/bookFilter.vue";
import bookList from "../components/bookList.vue";
import addBook from "../components/addBook.vue";

export default {
  name: "bookApp",
  components: {
    bookFilter,
    bookList,
    addBook,
  },
  methods: {
    selectBook(bookId) {
      this.$router.push({
        name: "bookDetails",
        params: {
          id: bookId,
        },
      });
    },
  },
  created() {
    this.$store.dispatch("loadBooks");
  },
};
</script>
<style lang="scss"></style>
